module.exports = "<div><h1><span><img src=" + __webpack_require__(752) + " alt=üWave class=Privacy-logo></span> Privacy Policy</h1><p>üWave attempts to store only the bare minimum of data necessary to make the site easy to use.</p><h2>Information About You</h2><p>üWave may log the following information about you when you visit the site:</p><ul><li>Your IP address and your &quot;User-Agent&quot;, i.e. the browser and browser version you are using, as well as the time of your visit.</li></ul><p>üWave stores the following information about you when you create a user account:</p><ul><li>Your username and email address;</li><li>Your encrypted password.</li></ul><p>It is not possible to delete your account yourself at this time. If you wish to delete this data, please ask the administrators.</p><h3>Third Parties</h3><p>üWave also uses some third-party services that may record information about you. These services are:</p><ul><li><p><a href=https://bugsnag.com>Bugsnag</a> records JavaScript errors so we can find and fix bugs more easily. Error reports contain the following potentially personally identifiable data:</p><ul><li>A unique number identifying your üWave account;</li><li>Your IP address;</li><li>The operating system and operating system version you are using;</li><li>The browser and browser version you are using.</li></ul><p>Error reports are stored for 7 days.</p></li></ul><h2>Information Stored on Your Computer</h2><p>When you log in to üWave, the site stores a &quot;cookie&quot; on your computer containing a unique token. This is used to log you in again automatically if you visit the site again on the same computer.</p></div>";

